img {
  margin-top: 30px;
  width: 600px;
  border-radius: 20%;
}
.loader {
  position: relative;
  height: 100px;
  width: 100px;
  border-bottom: 3px solid #ff3d00;
  box-sizing: border-box;
  animation: drawLine 4s linear infinite;
}
.loader:before {
  content: '';
  position: absolute;
  left: calc(100% + 14px);
  bottom: -6px;
  width: 16px;
  height: 100px;
  border-radius: 20px 20px 50px 50px;
  background-repeat: no-repeat;
  background-image: linear-gradient(#ff3d00 6px, transparent 0),
    linear-gradient(45deg, rgba(0, 0, 0, 0.02) 49%, black 51%),
    linear-gradient(315deg, rgba(0, 0, 0, 0.02) 49%, black 51%),
    linear-gradient(to bottom, black 10%, #ff3d00 10%, #ff3d00 90%, #ff3d00 90%);
  background-size: 3px 3px, 8px 8px, 8px 8px, 16px 88px;
  background-position: center bottom, left 88px, right 88px, left top;
  transform: rotate(25deg);
  animation: pencilRot 4s linear infinite;
}

@keyframes drawLine {
  0%,
  100% {
    width: 0px;
  }
  45%,
  55% {
    width: 200px;
  }
}

@keyframes pencilRot {
  0%,
  45% {
    bottom: -6px;
    left: calc(100% + 14px);
    transform: rotate(25deg);
  }
  55%,
  100% {
    bottom: -12px;
    left: calc(100% + 16px);
    transform: rotate(220deg);
  }
}
.is-hidden {
  display: none;
}
.cat-info {
  display: flex;
}
.descr {
  width: 600px;
  margin-top: 30px;
  margin-left: 50px;
  font-size: 22px;
}
.error {
  font-size: 30px;
  color: orange;
}
